                                                                        data-target="#modal-remover-usuario" data-toggle="modal" @click="abrirModalRemoverAcesso(conta, usuario)">
                                                                    <i class="mdi mdi-close-thick font-size-12"></i> 
                                                            </button>    
                                                        </span>
                                                    
                                                </li>
                                                <li v-if="conta.usuarios.length == 0">
                                                    <p>Nenhum usuário vinculado a esta conta</p>
                                                </li>
                                            </ul>
                                        </td>
                                        <td class="text-right">
                                            <button class="btn btn-outline-primary waves-effect waves-light" style="padding: 3px 12px 3px 12px;" title="Adicionar Usuário"
                                                        data-toggle="modal" data-target="#modal-adicionar-usuario" data-backdrop="static"  @click="abrirModalConcederAcesso(conta)">
                                                        <i class="mdi mdi-plus-box font-size-14"></i> Usuário
                                            </button>
                                        </td>
                                    </tr>
