                            </table>
                        </div>
                    </div>
                </div>                

                
            </div>
        </div>


        <div class="modal fade" id="modal-remover-usuario" tabindex="-1" aria-labelledby="modal-remover-usuario-title" aria-hidden="true" style="display: none;">
            <div class="modal-dialog modal-lg modal-dialog-scrollable">
                <div class="modal-content" >
                    <div class="modal-header">
                        <h4 class="modal-title mt-0" id="modal-remover-usuarioTitle"> Revogar Acesso</h4>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">×</span>
                        </button>
                    </div>
                    <div class="modal-body" v-if="objetoModal && objetoModal.usuario" >
                        <div class="row">
                            <div class="col-sm-12">
                                
                            </div>

                            <div class="col-sm-12">
                                <h4 >Tem certeza que deseja revogar o acesso do usuário: 
                                   <strong style="color:red">{{objetoModal.usuario.nome}}</strong> 
                                    da conta <span style="color:red">{{objetoModal.nome}}</span>  </h4>

                            </div>

                            
                        </div>
                    </div>
                    <div v-else class="modal-body"></div>
                    <div class="modal-footer" v-if="erroUsuario ||sucessoUsuario ">                      
                        <mensagem-componente titulo="Erro!"  :erros="erroUsuario" @fechar="erroUsuario = null"></mensagem-componente>
                        <mensagem-componente  :sucesso="sucessoUsuario" @fechar="sucessoUsuario = null"></mensagem-componente>                        
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">  <i class="mdi mdi-close-circle font-size-18"></i> Fechar</button>                        
                        <button type="button" class="btn btn-danger" :disabled="sucessoUsuario || !objetoModal || !objetoModal.usuario" @click.prevent="desvincularUsuario()"><i class="mdi mdi-content-save font-size-18"></i> Revogar Acesso </button>
                    </div>
                </div>
            </div>
        </div>


        <div class="modal fade" id="modal-adicionar-usuario" tabindex="-1" aria-labelledby="modal-adicionar-usuario-title" aria-hidden="true" style="display: none;">
            <div class="modal-dialog modal-lg modal-dialog-scrollable">
                <div class="modal-content" style="min-height: 400px">
                    <div class="modal-header">
                        <h4 class="modal-title mt-0" id="modal-adicionar-usuarioTitle"> Conceder Acesso</h4>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">×</span>
                        </button>
                    </div>
                    <form class="modal-body" v-if="objetoModal" >
                        <div class="row">
                            <div class="col-sm-12">
                                <h4>Conta: {{objetoModal.codigo}} - <strong> {{objetoModal.nome}}</strong></h4>
                            </div>

                            <div class="col-sm-12">
                                <h4>Usuário</h4>
                                <container-componente label="Usuário">
                                    <vue-select style="height: calc(1.5em + 0.94rem - 6px)"  placeholder="Comece a digitar para buscar"  :reduce="u => u.id"
                                                :options="usuarios" label="nome" @search="buscarUsuariosDebounce"
                                                v-model="objetoModal.usuario_id">
                                        <div slot="no-options">Nenhum item encontrado!</div>
                                        <div class="spinner" v-show="buscandoUsuarios">Buscando...</div>
                                    </vue-select>
                                </container-componente>
                            </div>
                            
                        </div>
                    </form>
                    <div v-else class="modal-body"></div>
                    <div class="modal-footer" v-if="erroUsuario ||sucessoUsuario ">                      
                        <mensagem-componente titulo="Erro!"  :erros="erroUsuario" @fechar="erroUsuario = null"></mensagem-componente>
                        <mensagem-componente  :sucesso="sucessoUsuario" @fechar="sucessoUsuario = null"></mensagem-componente>                        
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">  <i class="mdi mdi-close-circle font-size-18"></i> Fechar</button>                        
                        <button type="button" class="btn btn-primary" :disabled="sucessoUsuario || !objetoModal || !objetoModal.usuario_id" @click.prevent="vincularUsuario()"><i class="mdi mdi-content-save font-size-18"></i> Conceder Acesso </button>
                    </div>
                </div>
            </div>
        </div>

        <div class="modal fade" id="modal-delete" tabindex="-1" aria-labelledby="modal-delete-title" aria-hidden="true" style="display: none;">
            <div class="modal-dialog modal-dialog-scrollable">
                <div class="modal-content">
                    <div class="modal-header">
                        <h4 class="modal-title mt-0" id="modal-deleteTitle">  Confirmação de Desativação Contas</h4>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">×</span>
                        </button>
                    </div>
                    <div class="modal-body" v-if="objetoModal">
                        <p v-if="!sucessoDeletar">Tem certeza que deseja desativar a Conta <strong> {{objetoModal.codigo}} </strong> ({{objetoModal.nome}}) ?</p>
                        <hr> 
                        <p>Qual conta deve receber os lançamento do {{objetoModal.nome}}?</p>
                        <container-componente label="Nova conta" >
                           
                            <select class="form-control"
                            v-model="objetoModal.nova_conta_id"
                             >
                                <option v-for="c in contasSubstitutas" :key="c.id" :value="c.id">{{c.codigo}} {{c.nome}}</option>
                            </select>                        
                        </container-componente>
    
                        <div v-if="sucessoDeletar" class="text-center" >
                            <div class="swal2-icon swal2-success swal2-icon-show" style="display: flex;">
                                <div class="swal2-success-circular-line-left" style="background-color: rgb(255, 255, 255);"></div>
                                <span class="swal2-success-line-tip"></span> <span class="swal2-success-line-long"></span>
                                <div class="swal2-success-ring"></div> <div class="swal2-success-fix" style="background-color: rgb(255, 255, 255);"></div>
                                <div class="swal2-success-circular-line-right" style="background-color: rgb(255, 255, 255);"></div>
                            </div>
                            <p class="success text-center font-size-18">{{sucessoDeletar}}</p>
                            
                        </div>
                        <mensagem-componente titulo="Erro!" v-if="erroDeletar" :erros="erroDeletar" @fechar="erroDeletar = null"></mensagem-componente>
                    </div>
                    <div v-else class="modal-body"></div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-danger" :disabled="sucessoDeletar || !objetoModal || !objetoModal.nova_conta_id " @click.prevent="deletar()"><i class="mdi mdi-trash-can font-size-18"></i> Desativar </button>
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">  <i class="mdi mdi-close-circle font-size-18"></i> Fechar</button>                        
                    </div>
                </div><!-- /.modal-content -->
            </div><!-- /.modal-dialog -->
        </div>

        <div class="modal fade" id="modal-editar" tabindex="-1" aria-labelledby="modal-editar-title" aria-hidden="true" style="display: none;">
            <div class="modal-dialog modal-lg modal-dialog-scrollable">
                <div class="modal-content">
                    <div class="modal-header">
                        <h4 class="modal-title mt-0" id="modal-editarTitle"> Cadastro de Conta</h4>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">×</span>
                        </button>
                    </div>
                    <form class="modal-body" v-if="objetoModal">
                        <div class="row">

                            <label class="col-sm-3 col-form-label">Código</label>
                            <div class="col-sm-9">
                                <input-componente   
                                    :readonly="true"
                                    v-model="objetoModal.codigo"
                                    />
                            </div>

                            <label class="col-sm-3 col-form-label">Nome</label>
                            <div class="col-sm-9">
                                <input-componente
                                    v-model="objetoModal.nome"
                                    />
                            </div>

                            <label class="col-sm-3 col-form-label">Descrição</label>
                            <div class="col-sm-9">
                                <input-componente
                                    v-model="objetoModal.descricao"
                                    />
                            </div>
                        </div>
                    </form>
                    <div v-else class="modal-body"></div>
                    <div class="modal-footer">
                        <div class="row">
                            <div class="col-md-12" v-if="erroEditar">
                                <mensagem-componente titulo="Erro!"  :erros="erroEditar" @fechar="erroEditar = null"></mensagem-componente>
                            </div>
                            <div class="col-md-12" v-if="sucessoEditar">                                
                                <mensagem-componente  :sucesso="sucessoEditar" @fechar="sucessoEditar = null"></mensagem-componente>
                            </div>
                            <div class="col-md-12">
                                <button type="button" class="btn btn-secondary" data-dismiss="modal">  <i class="mdi mdi-close-circle font-size-18"></i> Fechar</button>                        
                                <button type="button" class="btn btn-primary" :disabled="sucessoEditar || !objetoModal" @click.prevent="salvar()"><i class="mdi mdi-content-save font-size-18"></i> Salvar </button>
                            </div>

                        </div>
                        
                        
                    </div>
                </div><!-- /.modal-content -->
            </div><!-- /.modal-dialog -->
        </div>
    </div>
</template>

<script>
import mensagem from '../../../components/forms/mensagem.vue';

import { ParametroSistemaService, PlanoContasService, UserService } from '../../../services'

const planoContasService = new PlanoContasService();
const parametroSistemaService = new ParametroSistemaService();
const userService = new UserService();

export default {
  components: { mensagem },

    data() {
        return {
            dados: [],
            filtro:{
                ordenar: 'codigo',
                ordem: 'asc',
                page: 1,
                per_page: 2000,
                texto: '',
            },
            paginacao: {
                current_page: 0,
                // total: 0,
                last_page: 0,
                from: 0,
                to: 0,
                per_page: 2000
            },
            debounce: null,
            objetoModal: null,
            sucessoDeletar: null,
            erroDeletar: null,
            sucessoEditar: null,
            erroEditar: null,

            erroConfiguracoes: null,
            sucessoConfiguracoes: null,
            carregadoConfiguracoes: false,

            contasBanco: [],
            contasCarteira: [],

            usuarios: [],
            erroUsuario: null,
            sucessoUsuario: null,
            buscandoUsuarios: false,            

            financeiro_ativo_bancos: {valor: '', label:'Grupo de Bancos', nome: 'financeiro_ativo_bancos'},
            financeiro_ativo_contas: {valor: '', label:'Grupo de Contas Carteira', nome: 'financeiro_ativo_contas'},
            financeiro_ativo_estoques: {valor: '', label:'Grupo de Contas de Estoque / Almoxarifado', nome: 'financeiro_ativo_estoques'},
            financeiro_ativo_clientes: {valor: '', label:'Conta de Clientes', nome: 'financeiro_ativo_clientes'},
            financeiro_ativo_capital_social: {valor: '', label:'Conta para Aportes', nome: 'financeiro_ativo_capital_social'},
            financeiro_ativo_bens: {valor: '', label:'Conta para Ativos Imobilizados / Bens', nome: 'financeiro_ativo_bens'},
            financeiro_passivo_fornecedores: {valor: '', label:'Grupo de Contas de Fornecedores', nome: 'financeiro_passivo_fornecedores'},
            financeiro_passivo_credores: {valor: '', label:'Grupo de Contas de Credores', nome: 'financeiro_passivo_credores'},
            financeiro_despesas_administrativas: {valor: '', label:'Grupo de Contas de Despesas Administrativas', nome: 'financeiro_despesas_administrativas'},
            financeiro_despesas_operacionais: {valor: '', label:'Grupo de Contas de Despesas Operacionais', nome: 'financeiro_despesas_operacionais'},
            financeiro_despesas_pessoal: {valor: '', label:'Grupo de Contas de Despesas Com Pessoal', nome: 'financeiro_despesas_pessoal'},
            financeiro_despesas_compras: {valor: '', label:'Conta para lançamento de despesas compras', nome: 'financeiro_despesas_compras'},
            financeiro_despesas_comissoes: {valor: '', label:'Conta para lançamento de despesas comissoes', nome: 'financeiro_despesas_comissoes'},
            financeiro_despesas_impostos: {valor: '', label:'Conta para lançamento de impostos', nome: 'financeiro_despesas_impostos'},
            financeiro_despesas_taxas_vendas: {valor: '', label:'Conta para lançamento de taxas de vendas', nome: 'financeiro_despesas_taxas_vendas'},
            financeiro_despesas_juros_credores: {valor: '', label:'Conta para lançamento de juros para credores', nome: 'financeiro_despesas_juros_credores'},
            financeiro_despesa_desconto_vendas: {valor: '', label:'Conta para lançamento de descontos concedidos na venda', nome: 'financeiro_despesa_desconto_vendas'},
            financeiro_despesas_dividendos: {valor: '', label:'Conta para lançamento de pagamentos de dividendos aos sócios', nome: 'financeiro_despesas_dividendos'},
            financeiro_despesas_depreciacao_ativo: {valor: '', label:'Conta de Depreciação de Imobilizados / Bens', nome: 'financeiro_despesas_depreciacao_ativo'},
            financeiro_despesas_descarte_ativo: {valor: '', label:'Conta de Descarte de Imobilizados / Bens', nome: 'financeiro_despesas_descarte_ativo'},

            financeiro_receita_vendas:{valor: '', label:'Conta para Lançamento de receita de Vendas', nome: 'financeiro_receita_vendas'},
            financeiro_receitas_taxas_vendas:{valor: '', label:'Conta para Lançamento de taxas de Vendas', nome: 'financeiro_receitas_taxas_vendas'},
            financeiro_receita_servicos:{valor: '', label:'Conta para Lançamento de receita de Serviços', nome: 'financeiro_receita_servicos'},
            financeiro_receita_desconto_compras:{valor: '', label:'Conta para Lançamento de receita de descontos de compras', nome: 'financeiro_receita_desconto_compras'},            
            financeiro_receitas_vendas_imobilizado: {valor: '', label:'Conta para lançamento de vendas do ativo imobilizado', nome: 'financeiro_receitas_vendas_imobilizado'},

        };
    },
    mounted() {
        this.buscar();
        setTimeout(() => {
            this.buscarConfiguracoes();
        }, 500);

    },
    methods: {

        novoItem: function(obj){
            this.sucessoEditar = null;
            this.erroEditar = null;

            /** Codigo */
            let codigo = obj.codigo+".";

            let codigos = this.dados.map(x => x.codigo).filter(x => x.indexOf(codigo) > -1);

            codigos = codigos.map(x => Number(x.substring( x.lastIndexOf('.')+1 )));
            
            let codigoX = codigos.length == 0 ? 1 : Math.max(...codigos) + 1;

            let pontos = codigo.split('.').length -1;
            if(pontos == 1){
                // codigoX deve ter dois digitos
                codigoX = String(codigoX).padStart(2,'0');
            };
            if(pontos == 2){
                // codigoX deve ter tres digitos
                codigoX = String(codigoX).padStart(3,'0');
            };

            codigo = codigo  + codigoX;
                
            
            this.objetoModal = {
                codigo: codigo,
                nome: '',
                descricao: ''
            };
            
        },

        abrirModalEditar:function(obj){
            this.sucessoEditar = null;
            this.erroEditar = null;
            this.objetoModal = {...obj};
        },

        salvar: function(){
            this.sucessoEditar = null;
            this.erroEditar = null;
            // objetoModal
            let req = this.objetoModal.id ? planoContasService.atualizar(this.objetoModal) : planoContasService.criar(this.objetoModal);

            req.then( response => {
                let obj = response.data;
                obj.nivel = obj.codigo.split('.').length;
                let i = this.dados.map(x => x.id).indexOf(obj.id);
                if(i > -1)
                    this.dados.splice(i,1,obj);
                else
                    this.dados.push(obj);
                
                if(this.objetoModal.usuarios)
                    obj.usuarios = [...this.objetoModal.usuarios]
                else
                    obj.usuarios = [];
                
                this.dados = this.dados.sort((a,b) =>  a.codigo > b.codigo ? 1 : -1);
                
                this.sucessoEditar = 'Conta salva com sucesso!';
            }).catch( erro => this.erroEditar = erro)            
        },

        abrirModalDeletar: function(obj){
            this.sucessoDeletar = null;
            this.erroDeletar = null;
            this.objetoModal = {...obj};
        },

        deletar: function(){
            this.sucessoDeletar = null;
            this.erroDeletar = null;

            planoContasService.deletar(this.objetoModal.id, this.objetoModal.nova_conta_id)
                .then( response => {
                    let i = this.dados.map(x => x.id).indexOf(this.objetoModal.id);
                    this.buscar()
                    this.sucessoDeletar = 'Registro removido com sucesso!';
                }).catch( erro => this.erroDeletar = erro)            
        },

        debounceBusca: function(){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.navegar(1);
            }, 800);
        },
        
        buscar:function(){
            planoContasService.listar(this.filtro).then(
                    response => {
                        this.$data.dados = response.data.data.map(x => {
                            x.nivel = x.codigo.split('.').length;
                            return x;
                        });
                    }
                )
        },

        navegar: function(pagina){
            this.filtro.page = pagina;
            this.buscar();
        },

        // Configurações
        buscarConfiguracoes: function(){
            if(this.dados.length == 0){
                setTimeout(() => {
                    this.buscarConfiguracoes();
                }, 1000);
                return 0;
            }
            parametroSistemaService.buscarConfiguracoesFinanceiro()
                .then( response => {
                    let dados = response.data;
                    for(let i = 0; i < dados.length; i++){
                        let conf = dados[i];
                        if(this.$data[conf.nome])
                            this.$data[conf.nome].valor = Number(conf.valor);
                    }
                    this.carregadoConfiguracoes = true;
                }).catch(
                    erro => {
                        console.log("Erro ao buscar configuracoes das contas: ",  erro.response);
                        setTimeout(() => {
                            this.buscarConfiguracoes();
                        }, 1000);
                    }
                )
        },

        atualizarConfiguracao: function(configuracao, novo){
            
            let obj = {...this.$data[configuracao]};
            let objNovo = {...this.$data[configuracao]};
            objNovo.valor = novo.target.value;
            parametroSistemaService.atualizarConfiguracaoFinanceiro(objNovo).
                then( response => {
                    this.$toast.success(`${obj.label} alterada com sucesso!`);
                    this.$data[configuracao].valor = objNovo.valor
                }).catch( erro => {
                    console.log(`Erro ao alterar ${obj.label}:!`, erro.response);
                    this.$data[configuracao].valor = obj.valor
                    this.$toast.error(`${obj.label}: Não foi possível alterar: ${erro.response.data.message}`);                    
                }).finally(() => this.$forceUpdate())
        },

        contasBancoCaixa:function(){
            let codigoBancoPai =  this.dados.filter(x => x.id == this.financeiro_ativo_bancos.valor);
            if(codigoBancoPai.length == 0){
                this.contasBanco = [];
            }else{
                codigoBancoPai = codigoBancoPai[0].codigo;
                this.contasBanco = this.dados.filter(x => {
                    if(x.codigo.length <= codigoBancoPai.length) return false;
                    let xx = x.codigo.substr(0, (codigoBancoPai.length));
                    return xx === codigoBancoPai;
                })
            }
            let codigoCarteiraPai =  this.dados.filter(x => x.id == this.financeiro_ativo_contas.valor);
            if(codigoCarteiraPai.length == 0){
                this.contasCarteira = [];
            }else{
                codigoCarteiraPai = codigoCarteiraPai[0].codigo;
                this.contasCarteira = this.dados.filter(x => {
                    if(x.codigo.length <= codigoCarteiraPai.length) return false;
                    let xx = x.codigo.substr(0, (codigoCarteiraPai.length));
                    return xx === codigoCarteiraPai;
                })
            }
        },

        // Usuarios

        buscarUsuariosDebounce: function(text, loading) {
            this.sucessoUsuario = null;
            this.erroUsuario = null;
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarUsuarios(text, loading);
            }, 600);
        },

        buscarUsuarios: function(text, loading){   
            this.buscandoUsuarios = true;             
            userService.listar({texto: text, per_page: 20})
                .then( response => {
                    this.usuarios = response.data.data;
                }).catch(error => console.log(error.response))                  
                .finally(() => this.buscandoUsuarios = true)
        },

        abrirModalConcederAcesso:function(conta){
            this.sucessoUsuario = null;
            this.erroUsuario = null;
            this.objetoModal = conta;
            this.buscarUsuarios();            
        },
        
        vincularUsuario: function(){
            this.sucessoUsuario = null;
            this.erroUsuario = null;
            
            planoContasService.vincularUsuario(this.objetoModal.id, this.objetoModal.usuario_id).then( response => {
                this.objetoModal.usuarios.push(response.data);
                this.sucessoUsuario = 'Usuário vinculado(a) com sucesso';
            }).catch( erro => this.erroUsuario = erro)
        },

        abrirModalRemoverAcesso:function(conta, usuario){
            this.sucessoUsuario = null;
            this.erroUsuario = null;
            this.objetoModal = conta;
            this.objetoModal.usuario = usuario;
        },
        desvincularUsuario: function(){
            this.sucessoUsuario = null;
            this.erroUsuario = null;
            
            planoContasService.desvincularUsuario(this.objetoModal.id, this.objetoModal.usuario.id).then( response => {
                this.objetoModal.usuarios = this.objetoModal.usuarios.filter(x => x.id != this.objetoModal.usuario.id);
                this.sucessoUsuario = this.objetoModal.usuario.nome + ' desvinculado(a) com sucesso';
            }).catch( erro => this.erroUsuario = erro)
        }
  
    },
    computed:{
        contasSubstitutas(){
